  opacity: 0;
  visibility: hidden;
  background: #fff;
  -webkit-box-shadow: 0px 0px 30px rgba(127, 137, 161, 0.25);
          box-shadow: 0px 0px 30px rgba(127, 137, 161, 0.25);
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-radius: 4px;
}
.navbar-ax .dropdown ul li {
  min-width: 200px;
}
.navbar-ax .dropdown ul a {
  padding: 10px 20px;
  font-size: 15px;
  text-transform: none;
}
